import AuthInput, { AuthContainer } from "../components/common/Auth";
import { AuthButton, AuthLinkText, AuthTitle } from "../components/common/Auth";
import { ReactComponent as Logo } from "../assets/icons/LOGO.svg";
import { Link } from "react-router-dom";

const LoginPage = () => {
  return (
    <AuthContainer>
      <Logo />
      <AuthTitle></AuthTitle>
      <AuthInput
        label=""
        placeholder=""
        alertText=""
        wordCount="10/15"
      />
      <AuthInput
        label=""
        placeholder=""
        alertText=""
        wordCount="10/15"
      />
      <AuthInput
        label="Email"
        placeholder=" Email"
        alertText=""
        wordCount="10/15"
      />

      <AuthInput type="password" label="" placeholder="" />
      <AuthInput
        type="password"
        label=""
        placeholder=""
      />
      <AuthButton></AuthButton>
      <Link to="/users/login">
        <AuthLinkText></AuthLinkText>
      </Link>
    </AuthContainer>
  );
};

export default LoginPage;
