import AuthInput, { AuthContainer } from "../components/common/Auth";
import {
  AuthButton,
  LinkTextContainer,
  AuthLinkText,
  AuthTitle,
} from "../components/common/Auth";
import { ReactComponent as Logo } from "../assets/icons/LOGO.svg";
import { Link } from "react-router-dom";

const LoginPage = () => {
  return (
    <AuthContainer>
      <Logo />
      <AuthTitle> Alphitter</AuthTitle>
      <AuthInput
        label=""
        placeholder=""
        alertText=""
        wordCount="10/15"
      />
      <AuthInput type="password" label="" placeholder="" />
      <AuthButton></AuthButton>
      <LinkTextContainer>
        <Link to="/users/signup">
          <AuthLinkText></AuthLinkText>
        </Link>
        
        <Link to="/admin/login">
          <AuthLinkText></AuthLinkText>
        </Link>
      </LinkTextContainer>
    </AuthContainer>
  );
};

export default LoginPage;
